.advantages {
  color: $color-dark-text;
  padding: 110px 100px 120px;

  @include vp-1023 {
    padding: 86px 45px 98px;
  }

  @include vp-767 {
    padding: 55px 15px 62px;
  }
}

.webp .advantages {
  background-image: url("../img/webp/advantages.webp");
  background-size: cover;
  background-color: $color-dark-text;

  @include vp-1024 {
    @include retina {
      background: url("../img/webp/advantages@2x.webp") no-repeat;
      background-size: cover;
    }
  }

  @include vp-1023 {
    background-image: url("../img/webp/advantages-tab.webp");
    background-size: cover;

    @include retina {
      background: url("../img/webp/advantages-tab@2x.webp") no-repeat;
      background-size: cover;
    }
  }

  @include vp-767 {
    background-image: url("../img/webp/advantages-mob.webp");
    background-size: cover;

    @include retina {
      background: url("../img/webp/advantages-mob@2x.webp") no-repeat;
      background-size: cover;
    }
  }
}

.no-webp .advantages {
  background-image: url("../img/advantages.png");
  background-size: cover;
  background-color: $color-dark-text;

  @include vp-1024 {
    @include retina {
      background: url("../img/advantages@2x.png") no-repeat;
      background-size: cover;
    }
  }

  @include vp-1023 {
    background-image: url("../img/advantages-tab.png");
    background-size: cover;

    @include retina {
      background: url("../img/advantages-tab@2x.png") no-repeat;
      background-size: cover;
    }
  }

  @include vp-767 {
    background-image: url("../img/advantages-mob.png");
    background-size: cover;

    @include retina {
      background: url("../img/advantages-mob@2x.png") no-repeat;
      background-size: cover;
    }
  }
}

.advantages h2 {
  @include font-semibold;

  font-size: 28px;
  line-height: 42px;
  letter-spacing: 0;
  text-align: center;
  margin: 0 0 50px 0;

  @include vp-1023 {
    letter-spacing: -0.05em;
  }

  @include vp-767 {
    font-size: 26px;
    line-height: 39px;
    margin: 0 0 30px 0;
  }
}

.advantages__list {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 40px;
  margin: 0 auto;
  padding: 0;
  max-width: 1160px;
  list-style-type: none;

  @include vp-1023 {
    grid-gap: 30px;
    width: 100%;
  }

  @include vp-767 {
    grid-template-columns: 1fr;
  }
}

.advantages__item {
  hyphens: auto;
  width: 100%;
  max-width: 360px;
  min-height: 241px;
  // height: 100%;
  background: $color-default-white;
  padding: 31px;

  @include vp-1023 {
    min-height: 310px;
    padding: 20px 10px 20px 20px;
  }

  @include vp-767 {
    max-width: 100%;
    min-height: 250px;
    padding: 20px;
  }
}

.advantages__item:nth-of-type(2) h3 br {
  display: none;

  @include vp-767 {
    display: block;
  }
}

.advantages__item:last-of-type h3 {
  @include vp-1023 {
    margin-bottom: 18px;
  }

  @include vp-767 {
    margin-bottom: 8px;
  }
}

.advantages__item h3 {
  @include font-medium;

  font-size: 18px;
  line-height: 25px;
  letter-spacing: 0;
  text-align: left;
  margin: 13px 0 8px;
}

.advantages__item p {
  @include font-regular;

  font-size: 14px;
  line-height: 20px;
  letter-spacing: 0;
  text-align: left;
  margin: 0;
}
