@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-400.woff2") format("woff2"), url("../fonts/Montserrat-400.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-500.woff2") format("woff2"), url("../fonts/Montserrat-500.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 600;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-600.woff2") format("woff2"), url("../fonts/Montserrat-600.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-700.woff2") format("woff2"), url("../fonts/Montserrat-700.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Placeholder", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.page__hidden {
  overflow: hidden;
}

.overlay {
  top: 0;
  left: 0;
  position: fixed;
  width: 100%;
  height: 100%;
  background: rgba(38, 19, 79, 0.487);
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.container {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 40px;
}
@media (max-width: 1439px) {
  .container {
    padding: 0 32px;
  }
}
@media (max-width: 1023px) {
  .container {
    padding: 0 24px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 16px;
  }
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(0, 0, 0, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal .modal__content {
  max-width: 403px;
  min-height: 455px;
  background: #011c40;
  padding: 50px;
  justify-self: end;
}
@media (max-width: 1023px) {
  .modal .modal__content {
    justify-self: center;
    max-width: 100%;
  }
}
@media (max-width: 767px) {
  .modal .modal__content {
    padding: 40px 38px;
  }
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 60px 40px;
}
@media (max-width: 767px) {
  .modal__wrapper {
    padding: 60px 16px;
  }
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  width: 600px;
  padding: 40px;
  background-color: #ffffff;
}
@media (max-width: 767px) {
  .modal__content {
    width: 100%;
    padding: 20px;
  }
}
.modal__close-btn {
  position: absolute;
  top: 22px;
  right: 22px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 767px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.header {
  padding: 0 20px;
}
@media (max-width: 1023px) {
  .header {
    padding: 0;
  }
}

.header__container {
  position: relative;
  margin: 0 auto;
  max-width: 1160px;
  display: grid;
  grid-template-columns: 1fr 2fr;
  align-items: center;
  margin-top: 35px;
  background: transparent;
}
@media (max-width: 1023px) {
  .header__container {
    margin-top: 28px;
    width: 100%;
    padding: 0 45px;
    grid-template-columns: 1fr 6fr;
  }
}
@media (max-width: 767px) {
  .header__container {
    width: 100%;
    padding: 0 15px;
    margin-top: 20px;
    grid-template-columns: 1fr 1fr;
  }
}

.header__logo {
  fill: #f9fbfd;
}
@media (max-width: 1023px) {
  .header__logo {
    width: 38px;
    height: 20px;
  }
}
@media (max-width: 767px) {
  .header__logo {
    position: relative;
    z-index: 1;
  }
}

.header__list {
  display: flex;
  justify-self: end;
  list-style-type: none;
  margin: 0;
  padding: 0;
  flex-wrap: wrap;
}
@media (max-width: 1023px) {
  .header__list {
    padding-right: 3px;
  }
}
@media (max-width: 767px) {
  .header__list {
    display: none;
    top: -35px;
    padding: 100px 0 0 25px;
    height: calc(100vh + 35px);
    width: calc(100vw - 35px);
    position: absolute;
    flex-direction: column;
    background: #ffffff;
  }
}

.header__burger-icon {
  display: none;
}
@media (max-width: 767px) {
  .header__burger-icon {
    justify-self: end;
    display: block;
  }
}

@media (max-width: 767px) {
  .header__burger-icon--open {
    display: block;
  }
}

@media (max-width: 767px) {
  .header__burger-icon--close {
    display: none;
    position: relative;
    z-index: 1;
  }
}

.header__item {
  margin-right: 33px;
}
@media (max-width: 1023px) {
  .header__item {
    margin-right: 23px;
  }
}
@media (max-width: 767px) {
  .header__item {
    margin: 0 0 26px 0;
  }
}

.header__item:last-of-type {
  margin-right: 0;
}

.header__link {
  font-family: "Montserrat", "Arial", sans-serif;
  font-weight: 500;
  font-style: normal;
  color: #ffffff;
  font-size: 16px;
  line-height: 20px;
  letter-spacing: 0;
  text-align: left;
}
.header__link:hover, .header__link:focus-visible {
  opacity: 0.7;
}
@media (max-width: 1023px) {
  .header__link {
    font-size: 14px;
    line-height: 17px;
  }
}
@media (max-width: 767px) {
  .header__link {
    font-size: 18px;
    line-height: 22px;
    color: #011c40;
  }
}

@media (max-width: 767px) {
  .header__list--no-js {
    display: block;
    height: auto;
  }
}

